import UserApi from '@/apis/UserApi'
import SNSApi from '@/apis/SNSApi'
import router from '../main'
const axios = require('axios').default
 /* eslint-disable no-unused-vars */
// initial state
const state = {
  JWT : localStorage.getItem('JWT'), //  
  userInfo : {}, // user  , ,   
  config : {},
  seq: 0,
  togosite:'',
}

// getters
const getters = {
  isLogin: function(state){
    var JWT = state.JWT
    if(JWT==null){
      return false
    }else{
      return true
    }
  }
}

// actions
const actions = {
  login({ commit },data){
    return UserApi.requestLogin(data,res=>{
      if(res.data.state==="ok"){
        commit('loginSuccess', res.data.JWT)
      }else{
        console.log(`  : ${res.data.message}`)
        // alert(res.data)
      }
    },error=>{
      console.log(`  ${error}`)
    })
  },

  logout({ commit }){
    axios.defaults.headers.common['Authorization'] = undefined
    axios.interceptors.request.eject();
    commit('logoutSuccess')
  },
  getUserInfo({ commit }){
    UserApi.requestUserInFo(res=>{
      if (res.data.state === 'ok'){
        commit('setUserInfo', res.data.message)
      }else{
        console.log(`    : ${res.data.message}`)
      }
    },error=>{
      //   :
      alert(`   .  `)
      console.log(error)
    })
  },
  SNSLogin({ commit }, data){
    console.log('  ',data.code)
    SNSApi[`${data.togosite}`]({code:data.code},
      res=>{
        if(res.data.state ==='ok'){
        //   
        commit('loginSuccess',res.data.token)
        }else{
          if (res.data.seq !== undefined){
          // togosite 
          commit('setSNSseq', {togosite:data.togosite, seq:res.data.seq})
          }else{
            // 
            console.log(res.data)
          }  
        }
      },err=>{
        console.log(err)
      })
    //    loginSuccess 
  },
  Join({ commit },data){
    UserApi.requestSignup(data,
      res=>{
        if(res.data.state === 'ok'){
          alert(' .')
          router.replace({name:'Login'})
        }else{
          //  ID  
          alert(res.data.message)
          console.log(res)
        }
      },err=>{
        console.log(err)
      })
  },
  SNSJoin({commit},data){
    console.log('Join  state ',state.seq)
    console.log('data ', data)
    data['seq'] = state.seq
    SNSApi.requestSNSJoin(data,
      res=>{
        if(res.data.state === 'ok'){
          router.push({name:'Login'})
        }else{
          console.log(res.data)
        }
      },err=>{
        console.log(err)
      })
  }

}
// mutations
const mutations = {
  loginSuccess(state, JWT){
    state.JWT = JWT
    localStorage.JWT = JWT
    // const accessToken= function(){
    //   const {JWT} = localStorage.JWT
    //   if (!JWT) return
    //   axios.defaults.headers.common['Authorization'] = `Bearer ${JWT}`;
    // }
    // accessToken()
    router.push({name:'Home'})
  },
  logoutSuccess(state){
    state.JWT = null
    delete localStorage.JWT;
  },
  setUserInfo(state, data){
    state.userInfo = data
    // console.log('', state.userInfo)
  },
  setSNSseq(state, data){
    state.seq = data.seq
    state.togosite = data.togosite
    console.log(' togosite ', data)
    router.push({name:'Join'})
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}