        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="userInfo.birth"
            label=""
            append-icon="mdi-calendar"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker 
        v-model="userInfo.birth" no-title scrollable
        :min=minDate
        :max=maxDate
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu.save(userInfo.birth)">OK</v-btn>
        </v-date-picker>
      </v-menu>
      </v-col>
          </v-row>
        </v-container>

        <v-card-actions>
        <v-text-field
          label=""
          v-model="userInfo.phone"
          :type="show1 ? 'text' : 'userInfo.phone'"
          placeholder="ex) 010-1234-1234"
        ></v-text-field>
        </v-card-actions>
     <v-card-actions>
      <v-file-input 
      accept="image/*" 
      label=" "
      v-model="imageData"
      @change="onUpload"
      ></v-file-input>
        </v-card-actions>

      <div style="width:100%;height:25px;"/>
      <v-divider class="mx-0"></v-divider>
      <div style="width:100%;height:25px;"/>

      <div v-if="this.$route.params.type==='Student'" id='StudentInfo'>

      <v-card-subtitle>SSAFY   </v-card-subtitle>
        <v-container fluid>
        <v-row align="center">
        <v-col class="d-flex" cols="12" sm="4">
        <v-select 
        v-model="userInfo.grade" 
        name="grade" 
        id="grade"
        label=" "
        :items="Info.grade"
        outlined
        ></v-select>
        </v-col>
        <v-col class="d-flex" cols="12" sm="8">
        <v-select
        v-model="userInfo.state" 
        name="state" 
        id="state"
        label=" "
        :items="Info.state"
        outlined
        >
        </v-select>
        </v-col>
        </v-row>
        <v-row align="center">
        <v-col class="d-flex" cols="12" sm="6">
        <v-select 
        dense
        v-model="userInfo.class1" 
        name="class1" 
        id="class1"
        label="1  "
        :items="Info.class1"></v-select>
        </v-col>
        <v-col class="d-flex" cols="12" sm="6">
        <v-select 
        dense
        v-model="userInfo.class2" 
        name="class2" 
        id="class2"
        label="2  "
        :items="Info.class2"
        ></v-select>
        </v-col>
        </v-row>
        </v-container>
    </div>
    <div v-else id='GeneralInfo'>
      <v-select 
        v-model="userInfo.utype" 
        name="type" 
        id="type"
        label=" "
        :items="Info.utype"
        outlined
        >
        </v-select>
    </div>
      
  </v-form>
        


    </v-form>
    <v-text-field
    type="password"
    v-model="originalPasswordCheck"
    placeholder="    "
    hide-details
    outlined
    style="margin-top:24px;"
    ></v-text-field>
    <v-btn 
    depressed
    block 
    color="custom_active white--text"
    :disabled="originalPasswordCheck=='' || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(originalPasswordCheck)"
    @click="submit"> </v-btn>
    </v-card>
  </v-dialog>

<!--    --------------------------------------------------------------------------------------------------------------------->
<v-dialog 
v-model="activityDialog" 
fullscreen hide-overlay 
transition="dialog-bottom-transition"
>

  <v-card>
    <v-toolbar flat dark color="primary">
      <v-btn icon dark @click="activityDialog = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>{{activityTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <!--     -->
    <component v-bind:is="activityDialog?currentComponent:'span'" :tabName="activityTabName"></component>
  </v-card>
</v-dialog>

<!--   --------------------------------------------------------------------------------------------------------------------->
<v-dialog 
v-model="signOutDialog" 
width="unset"
>
  <v-card
  max-width="400"
  style="padding:12px;"
  >
