  <v-card-title class="subtitle" style="color:red;">  ?</v-card-title>
  </v-card-actions>
  <v-card-actions>
    <v-text-field
      type = "password"
      v-model="signOutPW"
      outlined
      label="*       "
      placeholder="  "
      color="red"
    ></v-text-field>
  </v-card-actions>
  <v-container fluid style="padding-top:0px;">
    <v-row align="center">
      <v-col class="d-flex" style="padding-top:0px">
      <v-btn text class="widfull title" @click="signOut">
        
      </v-btn>
