<!--    --------------------------------------------------------------------------------------------------------------------->
<v-dialog 
v-model="etcDialog" 
fullscreen hide-overlay 
transition="dialog-bottom-transition"
>

  <v-card>
      <v-btn 
      icon 
      @click="etcDialog = false"
      style="margin:12px;"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
    <!--     -->
    <component v-bind:is="etcDialog?etcComponent:'span'"></component>
