<template>
  <div>
    <h1>/  </h1>
    <a  v-bind:href="link"> </a><br>
    <input v-model="email" 
      id="email" placeholder=" ."
      type="text"/>
    <button @click="sendEmail">   </button>
  </div>
</template>

<script>
import UserApi from '../../apis/UserApi'


export default {
  data: () => {
    return {
      email:'',
      link:'https://edu.ssafy.com/comm/login/SecurityLoginForm.do',
    }
  },
  methods:{
    sendEmail(){
      UserApi.requestfindPw({email: this.email},
        res=>{
          if(res.data.state !== 'ok'){
            alert('  .')
            this.$router.push({name: 'Login'})
          }
          console.log(res)
        },error=>{
          console.log(error)
        }
      )
    }
  }
}
</script>

