<template>
  <div id="posting" style="width:100%;">

<!-- header-->
      <v-card
        max-width="444"
        min-width="344"
    class="mx-auto"
    outlined
    style="padding:0px 0px 0px 0px;margin:0px 0px 0px 0px;width:344px;"
      >
        <v-card-actions v-if="step==1">
          <v-card-title style="padding:10px;"> </v-card-title><v-spacer/>
         <!--  -->
         <span class="caption" style="margin-left:12px;"></span>
          <v-checkbox
            v-model="postingForm.anonymous"
            value=1
            dense
            hide-details
            style="width:10%;margin-top:0px;"
        ></v-checkbox>
        </v-card-actions>
        <v-card-actions v-if="step==2">
        <v-card-title style="padding:10px;"> </v-card-title><v-spacer/>
        <v-btn color="primary" dark depressed @click="vote_show = !vote_show">
        <v-icon size="20">mdi-vote</v-icon>
      <span v-if="!vote_show" class="body-2"> </span>
      <span v-else class="body-2"> </span>
      </v-btn>
        </v-card-actions>
        <v-divider></v-divider>

<v-window v-model="step" touchless>

<v-window-item :value="1">
<!-- [step:1]  -->
  <v-card-text  
    class="py-0"
    style="margin-top:10px;"
  >
  <span class="subheading" style="padding-left:10px;">#HashTags</span>
  <v-chip-group column >
    <v-chip
      v-for="(tag, i) in Tags"
      :key="i"
      class="mr-2"
      :ripple="false"
    >
      {{ tag }}
    </v-chip>
  </v-chip-group>
  </v-card-text>
<!-- [step:1] -->
        <v-container fluid style="padding-bottom:0px;">
          <v-textarea
           v-model="content"
           :rules="[v => !!v|| ' ']"
            name="input-7-1"
            filled
            rows="9"
            value="The Woodman set to work at once, and so sharp was his axe that the tree was soon chopped nearly through."
          ></v-textarea>
        </v-container>
<!-- [step:1] footer-->
        <v-divider></v-divider>
        <v-card-actions>
          
          <v-spacer></v-spacer>
          <!--   -->
          <!-- @click="dialog = false" -->

          <div id="preview" v-if="uploadValue===100" style="height:40px;">
            <img 
              :src="postingForm.attachments" 
              style="width:100%;height:100%;object-fit:contain"
            />
            <v-icon color="green">mdi-check</v-icon>
          </div>
          <div id="circular" v-if="uploadValue==1">
            <v-progress-circular
              :size="15"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </div>
          <v-btn color="primary" text @click="$refs.inputUpload.click();uploadValue=1">Photo</v-btn>
          <input v-show="false" ref="inputUpload" type="file" @change="onUpload" >
          <v-btn color="primary" text @click="goStep2()">Next</v-btn>
        </v-card-actions>
          
</v-window-item>

<!-- <step:2> -->
<v-window-item :value="2">
<v-card-text  
    class="py-0"
    style="margin-top:10px;"
>
  <span class="subheading" style="padding-left:10px;">#HashTags</span>

  <v-chip-group
    v-model="selectedIndex"
    column
    style="padding-bottom:27px;"
  >
    <v-chip
      v-for="(tag, i) in selectedTag"
      :key="i"
      class="mr-2 primary"
      close
      color="white"
      text-color="white"
      @click:close="selectedTag.splice(i,1)"
    >
      {{ tag }}
    </v-chip>
  </v-chip-group>

  </v-card-text>
    <!--    -->
    <v-card-actions style="padding-left:12px;">


<v-expand-transition >
  <v-card-text  
  v-show="vote_show"
    class="py-0"
    style="padding:0px 60px 10px 60px;"
>
  <!--<span class="subheading" style="padding-left:10px;"> </span>-->
  <!---->
  <v-form>
    <v-text-field label=""
    v-model="vote.title"
    color="grey"
    :counter="30"
    style="margin-bottom:20px"
    >
    </v-text-field>
    <v-text-field label="  "
    outlined
    hide-details
    color="#4181a1"
    v-model="vote.a_name"
    >
    </v-text-field>
    <div style="width:100%;height:40px;text-align:center;margin-top:12px;"><span class="body-2" style="color:gray">vs</span></div>
    <v-text-field label="  "
    outlined
    color="#d1534f"
    v-model="vote.b_name"
    >
    </v-text-field>
  </v-form>
  </v-card-text>
</v-expand-transition>
  </v-card-actions>
  <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="blue darken-1" text @click="step=1">back</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="doposting">Done</v-btn>
        </v-card-actions>  

</v-window-item>



<v-window-item :value="3">
  <v-card-actions style="padding-top:20px;padding-bottom:0px;">
    <!-- <v-card-title style="position:center">
    <v-icon size="50" color="green">mdi-emoticon-happy-outline</v-icon>
    </v-card-title> -->
  </v-card-actions>
  <v-card-actions style="padding-top:0px;padding-bottom:20px;">
    <v-spacer></v-spacer>
    <v-card-title> </v-card-title>
    <v-spacer></v-spacer>

  </v-card-actions>
  
</v-window-item>



</v-window>
      </v-card>
  </div>
</template>
 



<script>
import presetData from '../../assets/preset'
import { mapActions, mapState } from 'vuex'
// import PostApi from '../../apis/PostApi' 
const firebase = require('firebase/app')
require('firebase/storage') 
  export default {
    data () {
      return {
        //---   ---
        dialog: false,
        selectedTag:[],
        content: '',
        tags:[], /* keyword:'', selected:false */
        presets:presetData,
        //---  ---
        postingForm:{
          anonymous: 0,
          attachments: '',
          comment:[],
          content:'',
          hashtag:'',
          id:'',
          like_check:false,
          like_count:0,
          nickname:'',
          postlike:[],
        },
        //step2 window stepper 
        step:1,
        selectedIndex:[],
        //image upload  
        imageData: null, 
        uploadValue: 0,
        img_name:'',
        //--  --()
        vote_show: false,
        vote: {
          title:'',
          a_name:'',
          b_name:''
        },
        //CSS
        circular:false,
      }
    },

    computed: {
      Tags () {
        if(!this.content) return []

        var tags = []
        var flag = false;

        //presets    
        for(const preset of this.presets){
          flag = false;
          for(const value of preset.values){
            if( this.content.toLowerCase().includes(value)){
              flag = true
              break;
            }
          }
          if(flag){
            tags.push(preset.keyword)
          }
        }
        //#    
        var pattern = /#([\w|-])*[^#\s]/g
        var sharps = this.content.match(pattern)
        
        if(sharps!=null&&sharps.length>0){
        tags = tags.concat(sharps)
         }

        return tags
      },
     ...mapState({
        userInfo: state=> state.user.userInfo,
      })
    },

    methods: {
      onUpload(e){
        this.imageData = e.target.files[0]
        this.img_name = e.target.name
        console.log(e.target.name)
        console.log('img object: ',this.imageData)
        const storageRef=firebase.storage().ref(`${this.imageData.name}`).put(this.imageData)
        storageRef.on(`state_changed`,snapshot=>{
          this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
        }, error=>{console.log(error.message)},
        ()=>{this.uploadValue=100;
          storageRef.snapshot.ref.getDownloadURL().then((url)=>{
            this.postingForm.attachments =url;
            console.log('url  ',this.postingForm.attachments)
          });
        })
      },
      doposting(){
        // content   
        // console.log(this.postingForm)
        if (this.content === ''){
          alert('    ')
          return
        }
        if (this.selectedTag.length === 0){
          alert('1  tag   ')
          return
        }
        //        
        if (this.vote_show===true){
          // console.log(this.vote)
          //   
          if (this.vote.title === ''){
            alert('  ')
            return
          }
          if (this.vote.a_name === ''){
            alert('  ')
            return
          }
          if (this.vote.b_name === ''){
            alert('  ')
            return
          }
        }
        this.step=3
        console.log(` ${this.selectedTag}`)
        this.postingForm.id = this.userInfo.id
        this.postingForm.content = this.content
        this.postingForm.hashtag = this.selectedTag.join('')
        this.posting({post:this.postingForm,vote:this.vote})
      },
       goStep2(){
         this.step=2;
        this.selectedTag = JSON.parse(JSON.stringify( this.Tags ))

      },
      goStep3(){
        this.step=3;
        this.selectedTag = JSON.parse(JSON.stringify( this.Tags ))
      },
    ...mapActions({
      posting:'post/doposting',
      getUserInfo:'user/getUserInfo',
      })
    },
    created(){
      this.getUserInfo()
    },
  }
</script>
<style scoped>
#preview img {
  max-width: 100%;
  max-height: 500px;
}
#posting{
  background-color: #fcfcfc;
}
</style>