<template>
  <div>
    <v-card
      max-width="444"
      height="unset"
      outlined
      class="mx-auto"
    >
    <v-card-actions>
    <v-card-title></v-card-title>
    <v-spacer/>
    <v-btn text @click="readAll">{{nmofNotification}}   </v-btn>
    </v-card-actions>
    <v-divider class="mx-0" style="margin-bottom:12px;"/>
    <!--  -------------------------------------------------->
   
      <!--  : active -->
      <v-card-actions v-for="noti in notices"  :key="noti.no"
      style="padding:0px;"
      >
      <v-chip label
        @click="check(noti)"
        dark
        :color="noti.checked==0?'custom_active white--text':'#aaaaaa'"
        style="width:100%;padding:30px 22px 30px 22px;margin:6px 18px 6px 18px"
        >
        <v-icon v-if="noti.checked!=0" color="#dddddd" style="margin-right:4px;">mdi-check</v-icon>
        {{noti.notificationMessage}}
      </v-chip>
      </v-card-actions>
      <!--   -->
      <v-divider class="mx-2" style="margin-top:12px;"/>



    <div style="width:100%;height:20px;"/>
    </v-card>
    <v-dialog
    width="unset"
    v-model="postDialog"
    >
    <Post :key="postObj.pno" :post="postObj"/>
    </v-dialog>
      

  </div>

</template>

<script>
/* eslint-disable no-unused-vars */
import NotificationApi from '../../apis/NotificationApi'
import PostApi from '../../apis/PostApi'
import Post from '../../components/Posts/Post'
export default {
  data: ()=>{
    return{
      notices :null,
      nmofNotification: 0,
      newalarm: false,

      //Css
      postDialog:false,
      postObj:{},
    }
  },
  components: {
    Post,
  },
  methods:{
    getAllNotification(){
      NotificationApi.getAllNotification(res=>{
        if (res.data.state==='ok'){
          this.notices = res.data.message
          // console.log(this.notices)
        }else{
          console.log(`   : ${res.data.message}`)
        }
      },err=>{
        console.log( `   : ${err}`)
      })
  },
  check(aNoti){
    //   
    var no = aNoti.no
    NotificationApi.ReadNotification({ no }, res=>{
      if (res.data.state === 'ok'){
        // console.log(res.data.message)
        //  
      this.getNmofNotification()
      this.getAllNotification()
      this.AlarmNotification()
      }
    },err=>{
      console.log( `   : ${err}`)
    })

    //  
    this.postDialog=true;
    this.postObj={};
    var pno = aNoti.pno
    PostApi.getPost({pno},
    res=>{
      this.postObj=res.data.message.post
    },
    err=>{
      alert('  ')
    }
    )
  },
  readAll(){
    var no;
    for(var i=0; i<this.notices.length ; i++){
      no = this.notices[i].no
      NotificationApi.ReadNotification({ no }, res=>{
      if (res.data.state === 'ok'){
        console.log(`  : ${res.data.message}`)
        //  
      this.getNmofNotification()
      this.getAllNotification()
      this.AlarmNotification()
      }
    },err=>{
      console.log(err)
    })
    }
  },
  getNmofNotification(){
    NotificationApi.CountNotification(res=>{
      if (res.data.state === 'ok'){
        this.nmofNotification = res.data.message
      }else{
        console.log( `    : ${res.data.message}`)
      }
    },err=>{
      console.log( `   : ${err}`)
    })
  },
  AlarmNotification(){
    NotificationApi.AlarmNotification(res=>{
      if (res.data.state==='ok'){
        this.newalarm = res.data.message
      }else{
        console.log( `    : ${res.data.message}`)
      }
    },err=>{
      console.log( `    : ${err}`)
    })
  }
},
created(){
  this.getNmofNotification()
  this.getAllNotification()
  this.AlarmNotification()
}
}
</script>

<style>
#notiblock{
  width:100%;
}
</style>